.loading-screen {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 100vh;
  background: #2222229c;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
}
.loading-ball{
  width: 25px;
  height: 25px;
  background: rgb(255, 255, 255);
  border-radius: 50%;
  margin: 73px auto;
  transition: all 2s ease-in;
  animation: ballLoading  2s 2s infinite;
  -webkit-animation: ballLoading  2s 2s infinite;
  -webkit-transition: all 2s ease-in;
  -moz-transition: all 2s ease-in;
  -ms-transition: all 2s ease-in;
  -o-transition: all 2s ease-in;
}

.loading-bar{
 
  width: 30px;
  height: 100px;
  border-radius: 30px;
  margin: auto;
 background-color:  #00000054;
}
@keyframes ballLoading{
  0%{
      margin: 73px auto
  }
  25%{
      margin: 2px auto
  }
  50%{
      margin: 73px auto
  }
  100%{
      margin: 2px auto
  }
}

